import React from 'react';
import { UserContext, Card } from './context';

function Balance() {
  const [show, setShow] = React.useState(true);
  const [status, setStatus] = React.useState('');
  const [balance, setBalance] = React.useState('');

  return (
    <Card
      bgcolor="info"
      header="Balance"
      status={status}
      body={show ? (
        <BalanceForm setShow={setShow} setStatus={setStatus} setBalance={setBalance} />
      ) : (
        <BalanceMsg setShow={setShow} balance={balance} />
      )}
    />
  );
}

function BalanceMsg(props) {
  return (
    <>
      <h5>Your Current Balance</h5>
      <p>{props.balance}</p>
      <button type="button" 
        className="btn btn-light" 
        onClick={() => props.setShow(true)}>
        Check balance again
      </button>
    </>
  );
}

function BalanceForm(props) {
  const [email, setEmail] = React.useState('');
  const ctx = React.useContext(UserContext);

  function handle() {
    const user = ctx.users.find((user) => user.email === email);
    if (!user) {
      props.setStatus('fail!');
      return;
    }

    props.setBalance(user.balance);
    console.log(user);
    props.setStatus('Your balance is: ' + user.balance);
    props.setShow(false);
  }

  return (
    <>
      Email<br/>
      <input type="input" 
        className="form-control" 
        placeholder="Enter email" 
        value={email} 
        onChange={e => setEmail(e.currentTarget.value)} /><br/>

      <button type="submit" 
        className="btn btn-light" 
        onClick={handle}>
        Check Balance
      </button>
    </>
  );
}

export default Balance;