import React from 'react';
import { UserContext, Card } from './context';  

function Deposit() {
  const [show, setShow] = React.useState(true);
  const [status, setStatus] = React.useState('');
  const ctx = React.useContext(UserContext);

  function handleDeposit(email, amount) {
    const user = ctx.users.find(user => user.email === email);
    if (!user) {
      setStatus('fail!');
      return;
    }

    ctx.addTransaction(email, Number(amount), 'deposit');
    setStatus('Deposit successful');
    setShow(false);
  }

  return (
    <Card
      bgcolor="warning"
      header="Deposit"
      status={status}
      body={show ? (
        <DepositForm handleDeposit={handleDeposit} />
      ) : (
        <DepositMsg setShow={setShow} />
      )}
    />
  );
}

function DepositMsg(props) {
  return (
    <>
      <h5>Success</h5>
      <button type="submit" className="btn btn-light" onClick={() => props.setShow(true)}>
        Deposit again
      </button>
    </>
  );
}

function DepositForm(props) {
  const [email, setEmail] = React.useState('');
  const [amount, setAmount] = React.useState('');

  function handle() {
    props.handleDeposit(email, amount);
  }

  return (
    <>
      Email<br />
      <input type="input" className="form-control" placeholder="Enter email" value={email} onChange={e => setEmail(e.currentTarget.value)} /><br />
      Amount<br />
      <input type="number" className="form-control" placeholder="Enter amount" value={amount} onChange={e => setAmount(e.currentTarget.value)} /><br />
      <button type="submit" className="btn btn-light" onClick={handle}>
        Deposit
      </button>
    </>
  );
}

export default Deposit;