import React from 'react';
import { UserContext, Card } from './context';

function History() {
  const ctx = React.useContext(UserContext);
  const [show, setShow] = React.useState(true);
  const [status, setStatus] = React.useState('');

  return (
    <Card
      bgcolor="info"
      header="Transaction History"
      status={status}
      body={show ? (
        <HistoryForm setShow={setShow} setStatus={setStatus} />
      ) : (
        <HistoryMsg setShow={setShow} transactions={ctx.users[0].transactions} />
      )}
    />
  );
}

function HistoryMsg(props) {
  return (
    <>
      <h5>Transactions Loaded</h5>
      <ul>
        {props.transactions.map((transaction, index) => (
          <li key={index}>
            {transaction.type} of ${transaction.amount} on {new Date(transaction.date).toLocaleString()}
          </li>
        ))}
      </ul>
      <button type="button" className="btn btn-light" onClick={() => props.setShow(true)}>
        Load Again
      </button>
    </>
  );
}

function HistoryForm(props) {
  function handleSubmit() {
    props.setShow(false);
  }

  return (
    <>
      <h5>Load Transactions</h5>
      <button type="button" className="btn btn-light" onClick={handleSubmit}>
        Load Transactions
      </button>
    </>
  );
}

export default History;