import React from 'react';
import ReactDOM from 'react-dom';
import { HashRouter, Route } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import NavBar from './components/NavBar';
import Home from './components/Home';
import CreateAccount from './components/CreateAccount';
import Login from './components/Login';
import Deposit from './components/Deposit';
import Withdraw from './components/Withdraw';
import Balance from './components/Balance';
import History from './components/History';
import AllData from './components/AllData';
import { UserContext } from './components/context';

function Spa() {
  const [users, setUsers] = React.useState([
    { name: 'Belinda', email: 'me@mit.edu', password: 'secret', balance: 100, transactions: [] }
  ]);

  const addTransaction = (email, amount, type) => {
    setUsers(prevUsers =>
      prevUsers.map(user =>
        user.email === email
          ? {
              ...user,
              balance: type === 'deposit' ? user.balance + amount : user.balance - amount,
              transactions: [...user.transactions, { type, amount, date: new Date().toISOString() }]
            }
          : user
      )
    );
  };

  return (
    <HashRouter>
      <div>
        <NavBar />
        <UserContext.Provider value={{ users, addTransaction }}>
          <div className="container" style={{ padding: "20px" }}>
            <Route path="/" exact component={Home} />
            <Route path="/CreateAccount/" component={CreateAccount} />
            <Route path="/login/" component={Login} />
            <Route path="/deposit/" component={Deposit} />
            <Route path="/withdraw/" component={Withdraw} />
            <Route path="/balance/" component={Balance} />
            <Route path="/history/" component={History} />
            <Route path="/alldata/" component={AllData} />
          </div>
        </UserContext.Provider>
      </div>
    </HashRouter>
  );
}

ReactDOM.render(<Spa />, document.getElementById('root'));